/* Colores  */
/* fonts */
/* responsive */
html {
  box-sizing: border-box;
  font-size: 62.5%;
  width: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "IBM Plex Mono", monospace;
  line-height: 2;
  font-size: 1.6rem;
  background-color: #68181f;
  margin: 0;
}

h1, h2, h3, h4 {
  font-family: "Germania One", system-ui;
  line-height: 1.2;
  margin: 0;
}

h1 {
  font-size: 4.8rem;
  color: #FFF;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

h4 {
  font-size: 2.8rem;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: #FFF;
  font-size: 2.4rem;
}

.contenedor {
  width: 90%;
  max-width: 120rem;
  margin: 0 auto;
}

@keyframes animacionDerecha {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes animacionIzquierda {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes aparecer {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.header {
  display: flex;
  flex-direction: column;
  z-index: 100;
  backdrop-filter: blur(5px);
}

.logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}
.logo img {
  width: 7rem;
  height: 7rem;
}

.navegador {
  display: flex;
  flex-direction: column;
  justify-content: right;
  align-items: center;
  gap: 0;
}
.navegador a {
  padding: 1rem;
}
.navegador a:hover {
  color: #efdbbf;
  transition: 500ms ease-in-out;
}
.navegador .contactameBtn {
  background-color: #efdbbf;
  border-radius: 15px;
  padding: 0.2rem 1rem;
  color: #121211;
}
.navegador .contactameBtn:hover {
  color: #ac3030;
}

.marginTop {
  margin-top: 2rem;
}

@media (min-width: 900px) {
  .header {
    position: fixed;
    flex-direction: row;
    justify-content: space-between;
    top: 0;
    left: 0;
    right: 0;
    box-shadow: 0 1px 10px #121211;
    padding: 1rem;
  }
  .logo {
    justify-content: start;
  }
  .navegador {
    flex-direction: row;
    gap: 2rem;
  }
  .marginTop {
    margin-top: 10rem;
  }
}
.hero {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero img {
  position: absolute;
  width: 40rem;
  margin-top: 10rem;
  height: auto;
  z-index: 2;
  border-radius: 10px;
  animation: animacionIzquierda 0.8s ease-in forwards;
}
.hero .palabras-hero {
  letter-spacing: 0.5rem;
  font-family: "Germania One", system-ui;
  font-size: clamp(8rem, 20vw, 20rem);
  line-height: 1;
}
.hero .palabras-hero .portafolio {
  color: transparent;
  z-index: 1;
  top: 0;
  left: 0;
  -webkit-text-stroke: 2px #efdbbf;
}
.hero .palabras-hero .portafolio-principal {
  position: relative;
  color: #FFF;
  z-index: 3;
  top: 0;
  left: 0;
}
.hero .palabras-hero .portafolio, .hero .palabras-hero .portafolio-principal {
  animation: animacionDerecha 0.8s ease-out forwards;
}

.cinta-negra {
  width: 100%;
  min-height: 22rem;
  margin-top: -5rem;
  background-color: #121211;
}

.redes {
  display: flex;
  flex-direction: row;
  align-items: end;
  justify-content: center;
  gap: 3rem;
  width: 100%;
  padding: 4rem 5rem;
  border-radius: 10px;
  text-align: justify;
}
.redes a {
  color: #FFF;
  font-size: 2rem;
  font-weight: 500;
  display: flex;
  align-content: center;
  gap: 1rem;
}
.redes a svg {
  align-self: center;
}

@media (min-width: 900px) {
  .hero img {
    width: 70rem;
  }
}
@media (min-width: 480px) {
  .hero img {
    width: 60rem;
  }
  .cinta-negra {
    margin-top: -10rem;
  }
}